	module.exports = {"data":{"markdownRemark":{"html":"<p>The homepage of a style guide should provide high-level information around what the design system is, what benefits it provides, who it's for, and how to get started with it. Like any good index page, it should provide clear navigation to key parts of the website.</p>\n<div class=\"u-margin-bottom-double\"><a href=\"/getting-started\" class=\"c-btn\">Get Started</a></div>\n<h2>Design System Benefits</h2>\n<p>Explain how the design system benefits users and the business. For inspiration, check out over 180 examples of design systems at <a href=\"http://styleguides.io/examples\">Styleguides.io</a>.</p>\n<h2>Contributing info</h2>\n<p>If it's desirable to have people from across the organization contribute to the design system, linking to the <a href=\"/contributing.html\">contributing page</a> from the homepage could be a good idea.</p>","frontmatter":{"path":"/","tags":null,"kicker":null,"title":"Style Guide Guides","description":"A boilerplate for creating superb style guides","status":null,"group":null}}},"pathContext":{"slug":"/"}}

